<template>
 
  <v-app>
  <Header v-on:tabchange="update" class="font" v-if="theme === 0"/>
  <HeaderNE v-on:tabchange="update" v-else-if="theme === 1"/>
  <v-content style="background-color:	#008080">
  <About v-on:close="about_open = false" v-if="about_open && theme === 0"/>
  <AboutNE v-on:close="about_open = false" v-else-if="about_open  && theme === 1"/>
  <Contact v-on:close="contact_open = false" v-if="contact_open  && theme === 0"/>
  <ContactNE v-on:close="contact_open = false" v-if="contact_open  && theme === 1"/>
   <Projects v-on:close="projects_open = false" v-if="projects_open && theme === 0"/>
  <ProjectsNE v-on:close="projects_open = false" v-if="projects_open && theme === 1"/>
  </v-content>
  <!-- <Footer v-on:changeTheme="changeTheme"/> -->
  <!-- <StartBar /> -->
  
  </v-app>
  
</template>

<script>

import About from './views/About'
import AboutNE from './views/AboutNE'
import Contact from './views/Contact'
import ContactNE from './views/ContactNE'
import ProjectsNE from './views/ProjectsNE'
import Projects from './views/Projects'
import Header from './views/Header'
import HeaderNE from './views/HeaderNE'
//import Draggable from './views/Draggable'
//import Footer from './views/Footer'
// import StartBar from './views/StartBar'
export default {
  name: 'App',

  components: {
    About,
    AboutNE,
    Contact,
    ContactNE, 
    ProjectsNE,
    Projects,
    Header,
    HeaderNE,
    //Draggable
    ///Footer,
    // StartBar
  },

  data: () => ({
    tab: 0,
    about_open: true,
    projects_open: false,
    contact_open: false,

    theme: 1
  }),
  methods: {
    update: function(a) {
      switch(a){
        case 0:
          this.about_open = true;
          break;
        case 1:
          this.projects_open = true;
          break;
        case 2:
          this.contact_open = true;
          break;
        default:
          break;
      }

      //console.log(a)
      //this.tab = a;
    },
    changeTheme : function(a) {
      this.theme = a;
      this.update(0)
    }
 }
};
</script>

<style scoped>
.active {
  color: wheat;
  text-decoration: none;
}
.not-active {
  color: white;
}
.link-style {
  text-decoration: none;
}
.basil {
  background-color: #FFFBE6 !important;
}
.basil--text {
  color: #356859 !important;
}
.font {
  font-family: 'Passion One';
}
</style>
